<template>
    <div class="container">
        <div class="main__info">
            <div class="main__left">
                <h2>{{ getCar.name }}</h2>
                <p>{{ getCar.text }}</p>
            </div>
            <div class="main__right">
                <img :src="getCar.img" alt="">
            </div>
        </div>
    </div>
</template>

<script>
import item from '@/bd';
    export default {
       data() {
        return {
            cars:item,
            currentId:null
        }
       },
       created() {
        this.currentId = this.$route.params.id
       }, 
       computed:{
        getCar(){
            return this.cars.find(car=> car.name == this.currentId)
        }
       }
    }
</script>

